.main {
  justify-content: center;
  display: flex;
}

.wrapper {
  width: 70%;
  max-width: 1100px;
  margin-top: 15px;
}

.wrapper-row {
  flex-direction: row;
  margin-bottom: 15px;
  display: flex;
}

.box-container {
  flex-basis: 100%;
  justify-content: center;
  display: flex;
}

.box-container.left {
  padding-right: 5px;
}

.box-container.right {
  padding-left: 5px;
}

@media screen and (max-width: 1200px) {
  .wrapper {
    width: 100%;
  }

  .wrapper-row {
    flex-direction: column;
    margin-bottom: 0;
  }

  .box-container.left {
    margin-bottom: 8px;
    padding-right: 3px;
  }

  .box-container.right {
    margin-bottom: 8px;
    padding-left: 3px;
  }
}

p.message {
  color: var(--gray-4);
  z-index: 15;
}

